











import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class HeaderItem extends Vue {
  @Prop()
  label!: string;

  @Prop()
  icon!: string;

  @Prop()
  size!: string;

  @Prop()
  event!: string;

  @Prop()
  disabledKey!: string;

  @Prop()
  isDisabledSave!: boolean;

  @Prop()
  isDisabledUndo!: boolean;

  @Prop()
  isDisabledRedo!: boolean;
}
